	module.exports = {"data":{"markdownRemark":{"html":"<h2>Bijectivity</h2>\n<h3>A bijective function is a function that is both injective and surjective.</h3>\n<h4>Injective Function</h4>\n<p> For a function to be injective, or one-to-one, every element of the codomain must be mapped\nto a unique element of the domain. Each X value has it’s own special Y value. </p>\n<p> <img src=\"http://images.tutorvista.com/cms/images/113/injective-function.png\" alt=\"injective\"> “An Injective Function”</p>\n<h4>Surjective Function</h4>\n<p> For a function to be surjective, or onto, every element codomain is mapped to be at least one element of the domain. Each Y value has at least one X value.</p>\n<p><img src=\"http://images.tutorvista.com/cms/images/113/surjective-function.png\" alt=\"surjective\"> “A Surjective Function”</p>\n<h4>Bijective Function</h4>\n<p>For a function to be a bijection, or a one-to-one correspondence, the function must be both injective and surjective. Every element of the codomain maps to exactly one element of the domain. The cardinality (or number of elements) of the codomain and the domain are equal.</p>\n<p><img src=\"http://images.tutorvista.com/cms/images/113/bijective-function.png\" alt=\"bijective\"> “A Bijective Function”</p>\n<h4>More Information:</h4>\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/Bijection,_injection_and_surjection\">wikipedia article on functions</a></li>\n<li><a href=\"http://www.tutorvista.com/content/math/different-types-of-functions/\">more functions</a></li>\n<li><a href=\"https://www.mathsisfun.com/sets/injective-surjective-bijective.html\">great for people new to math</a></li>\n</ul>","fields":{"slug":"/mathematics/functions/bijectivity/"},"frontmatter":{"title":"Bijectivity"}}},"pathContext":{"slug":"/mathematics/functions/bijectivity/"}}

